.topIcon1{
    font-size: 20px;
    margin-right: 10px;
    color: #9146FF;
    cursor: pointer;
}

.topIcon2{
    font-size: 20px;
    margin-right: 10px;
    color: #9146FF;
    cursor: pointer;
}

.topImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    cursor: pointer;
}

.topList{
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style: none;
}

.topListItem{
    margin-right: 20px;
    font-size: 18px;
    font-weight: 300;
    cursor: pointer;
}

.topSearchIcon{
    font-size: 18px;
    color: #E1C06E;
    cursor: pointer;
    margin-left: 15px;
}

